





















import Vue from 'vue'
export default Vue.extend({
  computed: {
    startDate: {
      set (ndate: string) {
        this.$store.commit('updateStart', ndate)
      },

      get (): string {
        return this.$store.state.sDate
      }
    },
    endDate: {
      set (ndate: string) {
        this.$store.commit('updateEnd', ndate)
      },

      get (): string {
        return this.$store.state.eDate
      }
    }
  },
  methods: {
    saveDate (): void {
      this.$store.commit('saveDates')
    }
  }
})
