


































import Vue from 'vue'
import axios from 'axios'
import moment from 'moment'
interface Order {
  id: number;
  orderDate: string;
  orderPriority: string;
  unitsSold: number;
  unitPrice: number;
  totalCost: number;
  totalRevenue: number;
  itemType: string;
}
class OrderModel {
  private orderDate: string;
  public id: number;
  OrderPriority: string;
  UnitsSold: number;
  UnitPrice: number;
  TotalCost: number;
  TotalRevenue: number;
  ItemType: string;
  constructor (orderMd: Order) {
    this.id = orderMd.id
    this.orderDate = orderMd.orderDate
    this.OrderPriority = orderMd.orderPriority
    this.UnitsSold = orderMd.unitsSold
    this.UnitPrice = orderMd.unitPrice
    this.TotalCost = orderMd.totalCost
    this.TotalRevenue = orderMd.totalRevenue
    this.ItemType = orderMd.itemType
  }

  get OrderDate (): string {
    return moment(this.orderDate).format('YYYY-MM-DD')
  }
}
export default Vue.extend({
  data () {
    const fields = [
      {
        key: 'OrderDate',
        label: 'Order Date'
      },
      {
        key: 'OrderPriority',
        label: 'Priority'
      },
      {
        key: 'UnitsSold',
        label: 'U.Sold'
      },
      {
        key: 'UnitPrice',
        label: 'U.Price'
      },
      {
        key: 'TotalCost',
        label: 'T.Cost'
      },
      {
        key: 'TotalRevenue',
        sortable: true,
        label: 'T.Revenue'
      },
      {
        key: 'ItemType',
        label: 'Item Type'
      }
    ]
    return {
      loading: true,
      pageNo: 1,
      pageSize: 8,
      fields: fields,
      items: new Array<OrderModel>()
    }
  },
  props: ['saleCount'],
  computed: {
    pageMax (): number {
      return Math.ceil(this.saleCount / this.pageSize)
    }
  },
  mounted () {
    this.getSales()
  },
  watch: {
    pageNo () {
      this.getSales()
    }
  },
  methods: {
    getSales (): void {
      this.loading = true
      axios.get('http://localhost:3000/sales/index', {
        params: {
          pageNo: this.pageNo,
          pageSize: this.pageSize
        }
      }).then(res => {
        const data: Order[] = res.data
        this.items = data.map(el => {
          return new OrderModel(el)
        })
        this.loading = false
      })
    },
    increment (): void {
      this.pageNo = this.pageNo + 1
    },
    decrement (): void {
      this.pageNo = this.pageNo - 1
    }
  }
})
